<template>
  <div class="container">
    <div class="page loading" v-if='loading==true'>
      <div class="loadingContainer">
        計算成績ing
      </div>
    </div>
    <div class="page" id='video' v-if='pageChannel=="video"'>
      <youtube :video-id="videoId" :player-vars="videoVars" player-width="100%" player-height="100%" @ended='playerEnd'></youtube>
      <div class="btn" @click='playerEnd'>skip</div>
    </div>
    <div class="page" id='form' v-if='pageChannel=="form"'>
      <div class="pageContainer">
        <div class="inputWrap">
          <input type="text" class='textInput' v-model='userData.hospital' placeholder="hospital"> 
          <br>
          <input type="text" class='textInput' v-model='userData.username' placeholder="name">
          <br>
          <!-- <input type="text" v-model.number='userData.score' placeholder="score">
          <br>
          <input type="text" v-model.number='userData.playTime' placeholder="playTime">
          <br>
          <input type="text" v-model='userData.serialNumber' placeholder="serialNumber">

          <br>  -->
          <input type="file" accept="image/*" id="capture" style='display:none;' capture="camera" ref='photoBtn' @change="onPhotoSelected">
          <br>
          <div class="btn" @click='$refs.photoBtn.click()'>photo</div>
          <br>
          <div class="btn" @click='uploadPhoto'>uploadPic & go rule </div>
          <br>
          <div class="btn" @click='uploadUserData'>upload Data </div>
        </div>
      </div>
    </div>
    <div class="page" id='rule' v-if='pageChannel=="rule"'>
      <div class="pageContainer">
        <div class="btn" @click='pageChannel="game"'>play</div>
      </div>
    </div>
    <div class="page" id='game' v-if='pageChannel=="game"'>
      <div class="pageContainer">
        <Game @gameover='gameover'></Game>
        <!-- <div class="btn" @click='pageChannel="rule"'>prev</div> -->
      </div>
    </div>
    <div class="page" id='board' v-if='pageChannel=="board"'>
      <div class="pageContainer">
        <div class="manContainer">
          <div class="man" v-for='(man,index) in sortScoreList' :key='index'>
            <span class="u_pic" :style='{ backgroundImage: "url("+ man.photoName  +")"}'></span>
            <span class="u_name">{{ man.username }}</span>
            <span class="u_scroe"> {{ man.score }}</span>
            <span class="u_playtime">{{man.playTime}}</span>
          </div>
          <div class="user man">
            <span class="u_pic" :style='{ backgroundImage: "url("+ userBoardPhoto  +")"}'></span>
            <span class="u_name">{{ userData.username }}</span>
            <span class="u_scroe"> {{ userData.score }}</span>
            <span class="u_playtime">{{userData.playTime}}</span>
          </div>
        </div>
        <div class="btn" @click='goIngredients'>finish</div>
      </div>
    </div>
    <div class="page" id='finish' v-if='pageChannel=="finish"'>
      <div class="pageContainer">
        <div class="btn" @click='pageChannel="Ingredients"'>Ingredients</div>
      </div>
    </div>
    <div class="page" id='Ingredients' v-if='pageChannel=="Ingredients"'>
      <div class="pageContainer">
        <div class="ice">
          <div class="Ingredient ing1 " v-if='iceArr.indexOf(1)!=-1'>1</div>
          <div class="Ingredient ing2 " v-if='iceArr.indexOf(2)!=-1'>2</div>
          <div class="Ingredient ing3 " v-if='iceArr.indexOf(3)!=-1'>3</div>
          <div class="Ingredient ing4 " v-if='iceArr.indexOf(4)!=-1'>4</div>
          <div class="Ingredient ing5 " v-if='iceArr.indexOf(5)!=-1'>5</div>
          <div class="Ingredient ing5 " v-if='iceArr.indexOf(6)!=-1'>6</div>
        </div>
        <div class="iceBtn">
          <div class="btn" @click="addIngredient(1)">ing1</div>
          <div class="btn" @click="addIngredient(2)">ing2</div>
          <div class="btn" @click="addIngredient(3)">ing3</div>
          <div class="btn" @click="addIngredient(4)">ing4</div>
          <div class="btn" @click="addIngredient(5)">ing5</div>
          <div class="btn" @click="addIngredient(6)">ing6</div>
        </div>
        <div class=" btn" @click='pageChannel="ice"'>ice</div>
      </div>
    </div>
    <div class="page" id='ice' v-if='pageChannel=="ice"'>
      <div class="pageContainer">
        <p>
          {{ serialNumber }}
        </p>
      </div>
    </div>
  </div>  
</template>

<script>
import axios from "axios";
import firebase from "firebase";
import Game from "./Game";

export default {
  name: "HelloWorld",

  data() {
    return {
      pageChannel: "video",
      loading: false,
      pageChannelArr: [
        "video",
        "form",
        "rule",
        "game",
        "board",
        "finish",
        "Ingredients",
        "ice"
      ],
      iceArr: [],
      userData: {
        hospital: "hospital",
        username: "brook",
        photoName: "test.png",
        score: 0,
        playTime: 0
      },
      serialNumber: '',
      userphoto: null,
      userBoardPhoto: '',
      videoId: "",
      videoArr: ["SfKQ9H0MWM0", "rQgytvHzark"],
      videoVars: {
        autoplay: 1,
        rel: 0,
        showInfo: 0
      },
      scoreList: []
    };
  },
  watch: {
    iceArr: function(arr) {
      console.log(arr);
      if (arr.length > 3) {
        this.iceArr.shift();
      }
    },
    userPhoto: function(photo) {
      console.log(photo)
      
    }
  },
  computed: {
    sortScoreList: function() {
      let obj = this.scoreList.sort(function(a, b) {
        return b["score"] - a["score"] || a["playTime"] - b["playTime"];
      });
      return obj;
    }
  },
  mounted() {
    // console.log(this.addZero(4,1084))
  },
  created() {
    console.log("created");
    // get url
    this.getUrlVideoId();
    // init fireBase
    this.initFirebase();
    // this.getBoardData();
    // this.getSerialNumber();
  },
  methods: {
    playerEnd() {
      this.pageChannel = "form";
    },
    gameover(data) {
      console.log(data.playTime, data.score);
      this.userData.score = data.score;
      this.userData.playTime = data.playTime;
      this.uploadUserData();
      // this.getBoardData();
    },
    onPhotoSelected(e) {
      let vm = this;
      this.userphoto = e.target.files[0];
      var reader = new FileReader();
      reader.onload = function() {
        vm.userBoardPhoto = reader.result;
      };
      reader.readAsDataURL(e.target.files[0]);
    },
    uploadPhoto(e) {
      if (
        this.userphoto == null ||
        this.userData.username == "" ||
        this.userData.hospital == ""
      ) {
        alert("請完成表單");
        return;
      }
      this.pageChannel = this.pageChannelArr[2];
      const formData = new FormData();
      formData.append("myFile", this.userphoto, this.userphoto.name);
      axios
        .post(
          "https://us-central1-gsk-319c7.cloudfunctions.net/uploadFile",
          formData
        )
        .then(e => {
          console.log(e);
          this.userData.photoName = e.data.data;
        });
    },
    uploadUserData() {
      console.log("upload data");
      this.database.push(this.userData).then(e => {
        this.pageChannel = this.pageChannelArr[4];
        this.getBoardData();
      });
    },
    getUrlVideoId() {
      let vIndex = +this.$route.query.vIndex - 1;
      console.log(vIndex);
      if (vIndex != NaN) {
        vIndex = vIndex < this.videoArr.length ? vIndex : 0;
        this.videoId = this.videoArr[vIndex];
        console.log(this.videoId);
      } else {
        console.log("NaN");
        this.videoId = this.videoArr[0];
      }
    },
    initFirebase() {
      var config = {
        apiKey: "AIzaSyAfSyBKDOds7c5qRLRHHg2Ug3RHLpNj_HE",
        authDomain: "gsk-319c7.firebaseapp.com",
        databaseURL: "https://gsk-319c7.firebaseio.com",
        projectId: "gsk-319c7",
        storageBucket: "gsk-319c7.appspot.com",
        messagingSenderId: "735059452550"
      };

      firebase.initializeApp(config);
      this.database = firebase.database().ref("posts/");
      this.serialDatabase = firebase.database().ref("serialNumber");
      this.storage = firebase.storage();
    },
    goIngredients(){
      this.getSerialNumber();
      this.pageChannel= 'Ingredients';
    },
    getBoardData() {
      this.loading = true;

      let vm = this;
      let storage = this.storage;
      this.database.limitToLast(10).once("value", function(data) {
        let scoreList = [];
        data.forEach(child => {
          scoreList.unshift(child.val());
        });
        prepareUrl(scoreList);
      });

      function prepareUrl(scoreList) {
        let count = 0;
        getUrl();
        function getUrl() {
          if (count >= scoreList.length) {
            console.log(scoreList);
            vm.scoreList = scoreList;
            vm.loading = false;
            return;
          }
          var imgRef = storage.ref("resized-" + scoreList[count].photoName);
          imgRef.getDownloadURL().then(url => {
            scoreList[count].photoName = url;
            count++;
            getUrl();
          });
        }
      }
    },
    addIngredient(ingId) {
      let index = this.iceArr.indexOf(ingId);
      let arr = this.iceArr;
      if (index != -1) {
        arr.splice(index, 1);
      } else {
        if (arr.length >= 3) {
          return;
        } else {
          arr.push(ingId);
        }
      }
    },
    getSerialNumber() {
      this.serialDatabase.once("value", data => {
        this.serialNumber = 1 + data.val();
        // console.log(this.serialNumber,data.val())
        this.serialDatabase.set(this.serialNumber);
        this.serialNumber = this.addZero(4, this.serialNumber);
      });
      // this.serialDatabase.set(1);
    },
    addZero(dig, num) {
      let count = dig;
      while (count > 0) {
        num = "0" + num;
        count--;
      }
      return num.slice(-dig);
    }
  },
  components: {
    Game
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
youtube {
  width: 100%;
  height: 100%;
}
.btn {
  padding: 15px;
  border: 1px solid #000;
  background-color: #ddd;
  cursor: pointer;
}
.textInput {
  padding: 15px;
  width: 100%;
  margin-top: 15px;
}
.loading {
  background-color: black;
  opacity: 0.7;
  color: white;
  z-index: 5;
}
.page {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
}
.u_pic {
  width: 50px;
  height: 50px;
  display: inline-block;
  background-size: cover;
}
</style>



